"use client";

import { useState } from 'react';
import { EventLog } from '../types/experiments';
import { formatDate } from '../utils/dateUtils';

interface EventLogPanelProps {
  logs: EventLog[];
}

export default function EventLogPanel({ logs }: EventLogPanelProps) {
  const [filter, setFilter] = useState<string>('all');
  
  const filteredLogs = filter === 'all' 
    ? logs 
    : logs.filter(log => log.eventType === filter);
  
  return (
    <div className="bg-white shadow-md rounded-lg p-6 h-full">
      <div className="flex flex-col md:flex-row justify-between items-start md:items-center mb-4">
        <h2 className="text-xl font-semibold">Event Log</h2>
        
        <div className="flex space-x-2 mt-2 md:mt-0 text-sm">
          <button
            onClick={() => setFilter('all')}
            className={`px-2 py-1 rounded ${filter === 'all' ? 'bg-blue-500 text-white' : 'bg-gray-100 text-gray-700'}`}
          >
            All
          </button>
          <button
            onClick={() => setFilter('visitor')}
            className={`px-2 py-1 rounded ${filter === 'visitor' ? 'bg-blue-500 text-white' : 'bg-gray-100 text-gray-700'}`}
          >
            Visitors
          </button>
          <button
            onClick={() => setFilter('conversion')}
            className={`px-2 py-1 rounded ${filter === 'conversion' ? 'bg-blue-500 text-white' : 'bg-gray-100 text-gray-700'}`}
          >
            Conversions
          </button>
          <button
            onClick={() => setFilter('milestone')}
            className={`px-2 py-1 rounded ${filter === 'milestone' ? 'bg-blue-500 text-white' : 'bg-gray-100 text-gray-700'}`}
          >
            Milestones
          </button>
        </div>
      </div>
      
      <div className="overflow-y-auto max-h-[600px] pr-2">
        {filteredLogs.length > 0 ? (
          <ul className="space-y-3">
            {filteredLogs.map((log) => (
              <LogItem key={log.id} log={log} />
            ))}
          </ul>
        ) : (
          <div className="text-center py-6 text-gray-500">
            No logs available
          </div>
        )}
      </div>
    </div>
  );
}

interface LogItemProps {
  log: EventLog;
}

function LogItem({ log }: LogItemProps) {
  const getEventTypeColor = (type: string) => {
    switch(type) {
      case 'visitor': return 'bg-blue-100 text-blue-800';
      case 'conversion': return 'bg-green-100 text-green-800';
      case 'milestone': return 'bg-purple-100 text-purple-800';
      case 'revenue': return 'bg-yellow-100 text-yellow-800';
      default: return 'bg-gray-100 text-gray-800';
    }
  };
  
  return (
    <li className="border border-gray-100 rounded-lg p-3 hover:bg-gray-50 transition-colors duration-150">
      <div className="flex justify-between items-start mb-2">
        <span className={`px-2 py-1 rounded text-xs font-medium ${getEventTypeColor(log.eventType)}`}>
          {log.eventType.charAt(0).toUpperCase() + log.eventType.slice(1)}
        </span>
        <span className="text-xs text-gray-500">{formatDate(log.timestamp)}</span>
      </div>
      <p className="text-sm text-gray-700">{log.message}</p>
      {log.data && (
        <div className="mt-2 text-xs bg-gray-50 p-2 rounded">
          <pre className="whitespace-pre-wrap">{JSON.stringify(log.data, null, 2)}</pre>
        </div>
      )}
    </li>
  );
}
