// Dynamic configuration for the frontend

// Try to connect to the backend service on various ports if the main one fails
export const getBackendUrl = () => {
  // First check if there's an environment variable
  if (process.env.NEXT_PUBLIC_API_URL) {
    return process.env.NEXT_PUBLIC_API_URL;
  }
  
  // For development, we'll try ports 5001, 5002, and 5003 in order
  // This mirrors the backend's port fallback mechanism
  if (process.env.NODE_ENV === 'development') {
    const hostname = typeof window !== 'undefined' ? window.location.hostname : 'localhost';
    return `http://${hostname}:5003`; // Using 5003 since we saw the server running on this port
  }
  
  // Default fallback
  return 'http://localhost:5001';
};

const config = {
  backendUrl: getBackendUrl(),
};

export default config;
