"use client";

import { Experiment } from '../types/experiments';

interface MetricsPanelProps {
  metrics: {
    totalVisitors: number;
    conversionRate: number;
    averageSessionDuration: number;
    revenuePerVisitor: number;
  } | null;
  experiment: Experiment;
}

export default function MetricsPanel({ metrics }: MetricsPanelProps) {
  const { totalVisitors, conversionRate, averageSessionDuration, revenuePerVisitor } = metrics || {};
  
  return (
    <div className="bg-white shadow-md rounded-lg p-6 mt-6">
      <h2 className="text-xl font-semibold mb-4">Key Metrics</h2>
      
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
        <MetricCard 
          title="Total Visitors" 
          value={totalVisitors || 0} 
          suffix="visitors"
          icon={<UserIcon />}
        />
        
        <MetricCard 
          title="Conversion Rate" 
          value={conversionRate?.toFixed(2) || 0} 
          suffix="%"
          icon={<ConversionIcon />}
        />
        
        <MetricCard 
          title="Avg. Session Duration" 
          value={formatDuration(averageSessionDuration || 0)}
          icon={<TimeIcon />}
        />
        
        <MetricCard 
          title="Revenue per Visitor" 
          value={`$${revenuePerVisitor?.toFixed(2) || 0}`}
          icon={<MoneyIcon />}
        />
      </div>
    </div>
  );
}

interface MetricCardProps {
  title: string;
  value: number | string;
  suffix?: string;
  icon: React.ReactNode;
}

function MetricCard({ title, value, suffix, icon }: MetricCardProps) {
  return (
    <div className="bg-gray-50 p-4 rounded-lg border border-gray-100">
      <div className="flex items-center mb-2">
        <div className="text-blue-500 mr-2">{icon}</div>
        <h3 className="text-sm font-medium text-gray-500">{title}</h3>
      </div>
      <div className="flex items-baseline">
        <p className="text-2xl font-bold">
          {value}
          {suffix && <span className="text-sm text-gray-500 ml-1">{suffix}</span>}
        </p>
      </div>
    </div>
  );
}

function formatDuration(seconds: number): string {
  const mins = Math.floor(seconds / 60);
  const secs = Math.floor(seconds % 60);
  return `${mins}m ${secs}s`;
}

// Simple icons for metrics
function UserIcon() {
  return (
    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
      <path fillRule="evenodd" d="M10 9a3 3 0 100-6 3 3 0 000 6zm-7 9a7 7 0 1114 0H3z" clipRule="evenodd" />
    </svg>
  );
}

