"use client";

import { useState, useEffect } from 'react';
import Header from './Header';
import MetricsPanel from './MetricsPanel';
import VisualizationSection from './VisualizationSection';
import EventLogPanel from './EventLogPanel';
import { useExperimentData } from '../hooks/useExperimentData';

export default function Dashboard() {
  const { experiments, metrics, logs, loading, error } = useExperimentData();
  const [lastUpdated, setLastUpdated] = useState<Date | null>(null);
  
  useEffect(() => {
    if (experiments.length > 0) {
      setLastUpdated(new Date());
    }
  }, [experiments]);

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
          <p>Error loading experiment data. Please try again later.</p>
        </div>
      </div>
    );
  }

  const activeExperiment = experiments[0] || null;
  
  return (
    <div className="min-h-screen bg-gray-50">
      <Header lastUpdated={lastUpdated} />
      
      <div className="container mx-auto px-4 py-6 max-w-screen-xl">
        {activeExperiment ? (
          <>
            <div className="mb-6">
              <h1 className="text-2xl font-semibold text-gray-800">Experiment Dashboard</h1>
              <p className="text-gray-500 mt-1">Monitor and analyze your active experiments in real-time</p>
            </div>
            
            <MetricsPanel metrics={metrics} experiment={activeExperiment} />
            
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mt-8">
              <div className="lg:col-span-2">
                <VisualizationSection experiment={activeExperiment} />
              </div>
              <div className="lg:col-span-1">
                <EventLogPanel logs={logs} />
              </div>
            </div>
          </>
        ) : (
          <div className="evolv-card p-8 text-center border border-gray-100">
            <div className="w-16 h-16 bg-blue-50 rounded-full flex items-center justify-center mx-auto mb-4">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-8 w-8 text-blue-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9.75 17L9 20l-1 1h8l-1-1-.75-3M3 13h18M5 17h14a2 2 0 002-2V5a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
              </svg>
            </div>
            <h2 className="text-xl font-semibold text-gray-800 mb-2">No Active Experiments</h2>
            <p className="text-gray-500 mb-6">There are currently no active experiments to monitor.</p>
            <button className="evolv-gradient-bg text-white px-5 py-2 rounded-md font-medium hover:opacity-90 transition-opacity">
              Create New Experiment
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
